class ResearchPage extends React.Component {
  constructor(props) {
    super();
    this.state = { myResearch: null };
  }
  componentDidMount() {
    if (typeof window !== "undefined") {
      this.setState({
        myResearch: [
          {
            title: "Do Urban Parks Promote Racial Diversity?",
            subtitle: "Evidence from New York City",
            id: "nyc-parks-diversity",
            year: 2021,
            authors: [],
            datavis: true,
            datavisLink: '/parks/',
            abstract: 
            "In this paper, I provide the first causal evidence to suggest that the provision of urban parks allows the city to promote opportunities for racially and ethnically diverse encounters. Utilizing a novel dataset featuring individual GPS tracking data for more than 60 thousand Twitter users in the New York metro area, I introduce a measure of racial diversity that captures ones level of exposure to diverse others in places visited daily. My empirical strategy relies on using the variation in the timing of park construction works across the city (which temporarily limit the capacity of said parks) to identify the impact of the effectively accessible parkland area on the individual exposure to racial diversity. My results show that for White and Black/African American residents additional 10 acres of parks within a 5 km radius from home increase individual chances of encounters with the members of other groups by 1 p.p. The effect is sizable: for reference, transitioning from the current state to the random mixing scenario would require a 9 p.p increase in diversity for an average Black or African American individual and a 3.5 p.p increase for an average White person. I also provide evidence to suggest that park accessibility affects the diversity of White and Black residents differently: for parks located closer to home, the effect appears to be more pronounced for Whites than Blacks.",
            pdflink: parksDiversityFile,
            carousel: <CarouselParks />,
            tags: [
              { name: "Job Market Paper", color: "oranLight", link: parksDiversityFile},
              { name: "Working Paper" ,link: parksDiversityFile},
              { name: "Download PDF" , link: parksDiversityFile},
            ],
            open: 1,
          },
          {
            title: "The Contribution of Urban Parks to Social Ties between Neighborhoods.",
            subtitle:
              "A Study of Online Friendship Networks in New York City.",
            year: 2021,
            id: "parks-social-ties",
            authors: [],
            pdflink: parksSocialTiesFile,
            tags: [{ name: "Working Paper",  link: parksSocialTiesFile }, {name: "Download PDF",  link: parksSocialTiesFile}],
            open: 1,
            abstract:
            "Although the role of open spaces in promoting social ties between neighborhoods has been widely discussed by social scientists and urban design scholars, data sets from online social net- works present unexplored opportunities to quantify this link on a larger scale. In this paper I use data on friendship links between Facebook users across New York city zip codes to show that two neighborhoods with higher density of green space between them are more likely to have stronger social ties. In particular, when controlling for demographic differences and zip-code level fixed ef- fects, I find that a 1 p.p. increase in the percentage of land allocated to parks between two given zip codes is associated with a 1.2% higher chance of online social connection between their residents. Comparing the effects of park density for different types of parks, I further document that the pres- ence of community parks, flagship parks, and playgrounds are all significant predictors of higher social connectedness between zip codes. Notably, the largest estimated effect is for playgrounds, indicating a 33% higher probability of connection per 1 p.p. increase in density."
          },
          {
            title: "The Effect of Co-location on Human Communication Networks",
            subtitle:
              "",
            year: 2022,
            id: "co-location-email-networks",
            authors: [
              {"name": "Carmody D., Mazzarello M., Santi P., Harris T., Lehmann S., Dunbar R., Ratti C."}
            ],
            pdflink: "https://arxiv.org/abs/2201.02230",
            tags: [{ name: "ArXiv Preprint",  link: "https://arxiv.org/abs/2201.02230" }, {name: "Download PDF",  link: "https://arxiv.org/pdf/2201.02230.pdf"}],
            open: 0,
            abstract:
            "The ability to rewire ties in communication networks is vital for large-scale human cooperation and the spread of new ideas. Especially important for knowledge dissemination is the ability to form new weak ties -- ties which act as bridges between distant parts of the social system and enable the flow of novel information. Here we show that lack of researcher co-location during the COVID-19 lockdown caused the loss of more than 4800 weak ties over 18 months in the email network of a large North American university -- the MIT campus. Furthermore, we find that the re-introduction of partial co-location through a hybrid work mode starting in September 2021 led to a partial regeneration of weak ties, especially between researchers who work in close proximity. We quantify the effect of co-location in renewing ties -- a process that we have termed nexogenesis -- through a novel model based on physical proximity, which is able to reproduce all empirical observations. Results highlight that employees who are not co-located are less likely to form ties, weakening the spread of information in the workplace. Such findings could contribute to a better understanding of the spatio-temporal dynamics of human communication networks -- and help organizations that are moving towards the implementation of hybrid work policies to evaluate the minimum amount of in-person interaction necessary for a healthy work life."
          },
          {
            title: "Do Local Businesses Benefit from Stadiums?",
            subtitle:
              "An Empirical Study of Major Sports Leagues Using Mobile Location Data",
            year: 2022,
            id: "stadiums-local-businesses",
            authors: [{ name: "Dmitry Sedov", link: "https://www.dsedov.io/" }],
            pdflink: stadiumsFile,
            tags: [{ name: "Working Paper",  link: stadiumsFile }, {name: "Download PDF",  link: stadiumsFile}],
            open: 0,
            abstract:
            "Stadium construction, which can cost hundreds of millions of dollars, is often subsidized by public sources. In many cases, subsidies are allocated on the premise that sports venues benefit the local economy by bringing new customers to nearby businesses. To pin down the size and the spatial distribution of such spillovers, we use daily foot traffic data covering major sports league arenas and the surrounding commercial establishments. By employing the fixed effects and the IV estimation strategies, we show that the spillover benefits are heterogeneous across sports and business sectors. We find that 100 baseball stadium visits generate roughly 29 visits to nearby food & accommodation businesses and about 6 visits to local retail establishments. While the estimates for football stadiums are comparable, basketball & hockey arenas do not appear to generate significant spillovers for the surrounding businesses. Using our spillover estimates, we also compute an upper bound on the additional local spending induced by each sample arena. The median value of the additional spending turns out to be substantially smaller than the corresponding stadium subsidy."
          }
        ],
      });
    }
  }
  render() {
    return (
      <Box>
        <span id="research"></span>
        <Box
          sx={{
            px: [0, 2, 2],
            pt: 2,
            mt: 3,
            // bg: 'red',
            pb: 2,
            borderTop: "2px solid",
            borderTopColor: "black",
            fontSize: ["10px", "12px", "15px"],
          }}
        >
          <h1>Research</h1>
          {/* <br/> */}
          <ResearchItems items={this.state.myResearch} />
        </Box>
      </Box>
    );
  }
}

export default ResearchPage;
